import React from 'react';
import  { Transition }  from 'react-transition-group';
import { inject, observer } from  'mobx-react';
import { Link } from 'react-router-dom';

import MenuButton from './MenuButton'; 
import './slide.css';

@inject('commonStore')
class LoggedOutView extends React.Component{
  render(){
      if (!this.props.currentUser) {
        return (
          <ul className="nav navbar-nav " onClick = {() => {
            this.props.commonStore.toggleSlide();
            if(this.props.commonStore.isSlideActive === true){
              document.getElementsByTagName("body")[0].style.overflow = "hidden"
            }else{
              document.getElementsByTagName("body")[0].style.overflow = "auto"
            }}}>
    
            <li className="nav-item">
              <Link to="/" className="nav-link">
                Home
              </Link>
            </li>
    
            <li className="nav-item">
              <Link to="/login" className="nav-link">
                Sign in
              </Link>
            </li>
    
            <li className="nav-item">
              <Link to="/register" className="nav-link">
                Sign up
              </Link>
            </li>
    
          </ul>
        );
      }
      return null;
  }
}

@inject('commonStore')
class LoggedInView extends React.Component{
  render(){
    if (this.props.currentUser){
    return (
      <ul className="nav navbar-nav " onClick = {() => {
        this.props.commonStore.toggleSlide();
        if(this.props.commonStore.isSlideActive === true){
          document.getElementsByTagName("body")[0].style.overflow = "hidden"
        }else{
          document.getElementsByTagName("body")[0].style.overflow = "auto"
        }}}>

        <li className="nav-item">
          <Link
            to={`/@${this.props.currentUser.username}`}
            className="nav-link"
          >
            <img src={this.props.currentUser.image} className="user-pic" alt="" />
            {this.props.currentUser.username}
          </Link>
        </li>

        <li className="nav-item">
          <Link to="/" className="nav-link">
            Home
          </Link>
        </li>

        <li className="nav-item">
          <Link to="/editor" className="nav-link">
            <i className="ion-compose" />&nbsp;New Post
          </Link>
        </li>

        <li className="nav-item">
          <Link to="/settings" className="nav-link">
            <i className="ion-gear-a" />&nbsp;Settings
          </Link>
        </li>

      </ul>
    )
  }
    return null;
  }
}

@inject('commonStore','userStore')
@observer
export default class Slide extends React.Component{
  defaultStyle = {
    transition: `all 200ms ease-in-out`,
    transform: 'translate(-100%,0px)',
    // 'transform-origin':0,
    display: 'inline-block',
    backgroundColor: 'rgb(2, 20, 41)',
    width: '70%',
    height: '100%',
    'min-width': '100px',
    'max-width': '320px',
    position:'absolute',
    'z-index':'3',
    left:'0',
    top:'0',
  }
  
  transitionStyles = {
    entering: { transform: 'translate(0,0)' },
    entered:{ transform: 'translate(0,0)' },
  };
  componentDidMount(){

  }
  render(){
    let  windowHeight = Math.max(
      document.documentElement.clientHeight, 
      window.innerHeight
    );
    this.defaultStyle.height = windowHeight+'px';
    return(
      <Transition in={this.props.commonStore.isSlideActive} timeout={200}>
        {(state) => (
          <div 
            style={{
              ...this.defaultStyle,
              ...this.transitionStyles[state]}}
          >
            <MenuButton/>
          <LoggedOutView currentUser={this.props.userStore.currentUser} />

          <LoggedInView currentUser={this.props.userStore.currentUser} />
          </div>
        )}
      </Transition>
    )
  }
};


