import React from 'react';
import { autorun } from 'mobx';
import { inject, observer } from  'mobx-react';
// import { parse as qsParse } from 'query-string';
import { withRouter } from 'react-router-dom';
import './SearchTag.css';

@inject('articlesStore')
@withRouter
@observer
export default class SearchTag extends React.Component{
  constructor(){
    super();
    this.state={
      tag:'',
      locationTag:''
    };
  }

  disposer(){}

  componentDidMount() {
    this.setState({
      tag:this.props.articlesStore.predicate.tag,
      locationTag:this.props.articlesStore.predicate.tag
    })  
    this.disposer = autorun(() => console.log(this.props.articlesStore.predicate.tag))
  }
  componentDidUpdate(){
    if(this.state.locationTag !== this.props.articlesStore.predicate.tag){
      this.setState({
        locationTag:this.props.articlesStore.predicate.tag,
        tag:this.props.articlesStore.predicate.tag,
      })
    }
  }
  componenWillUnmount(){
    this.disposer();
  }

  handleChange = e => {
    this.setState({ tag: e.target.value,locationTag: this.props.articlesStore.predicate.tag});
  };

  handleSubmitForm = e => {
    e.preventDefault();
    if(!!this.state.tag){
      this.props.history.replace(`?tab=tag&tag=${this.state.tag||''}`)
    }
  };

  render(){
    let tag=this.state.tag;
    if(this.state.locationTag !== this.props.articlesStore.predicate.tag){
      tag=this.props.articlesStore.predicate.tag;
    }

    return (
      <form className="search-tag" onSubmit={this.handleSubmitForm}>  
        <fieldset>
          <legend>:</legend>
          {/* <label for="search_tag"></label> */}
          <input type="text"
            id="search_tag"
            name="searchTagName"
            placeholder={""}
            value={tag||''}
            onChange={this.handleChange}
          />
          <input type="submit"
            value="&#x1f50e;"
          />
          
        </fieldset>
      </form>
    )
  }
}




